import React, { Fragment, useEffect } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { dt } from "../../i18n";

export default function AllCategories() {
	const { menuVertical } = useSelector(state => state.menu);

	const scroll = window.addEventListener("scroll", () => {
		const div = document.querySelector(".all-categories");
		if (div) {
			if (window.scrollY > 140) {
				div.classList.add("fixed");
			} else div.classList.remove("fixed");
		}
	});

	useEffect(() => {
		return () => {
			window.removeEventListener("scroll", scroll);
		};
	}, []);

	useEffect(() => {
		const catElements = document.querySelectorAll(".idElements");
		const anchors = document.querySelectorAll(".anchor");
		anchors.forEach(a => {
			a.addEventListener("click", e => {
				e.preventDefault();
				catElements.forEach(el => {
					if (a.href.includes(el.id)) {
						window.scrollTo(0, el.offsetTop + 100);
						el.classList.add("text-primary");
					} else el.classList.remove("text-primary");
				});
			});
		});
	}, [window.location.hash]);

	if (!menuVertical) {
		return "";
	}

	return (
		<div className="all-categories">
			<div className="row">
				<div className="col-12 menu">
					<div className="container d-flex align-items-center flex-wrap">
						{menuVertical?.map((item, index) => {
							return (
								<a
									href={`/all-categories/#${item.slug}`}
									key={index}
									className="col-lg-3 col-md-4 anchor  col-6"
								>
									<h4 className={` menu-item d-flex`}>
										{item.image ? (
											<img
												src={item.image.urls["100x_"]}
												alt={item.image.originalName}
											/>
										) : null}
										<span>{dt(item.name)}</span>
									</h4>
								</a>
							);
						})}
					</div>
				</div>
				<div className="col-12 menus">
					<div className="container">
						<div className="row">
							{menuVertical.map((item, i) => (
								<Fragment key={i}>
									<Link
										id={item.slug}
										to={`/category/${item.slug}`}
										className={`category-title idElements col-12`}
									>
										{dt(item.name)}
									</Link>
									{item.children.map((category, index) => {
										return (
											<div
												className="col-6 row m-0 mb-2"
												key={index}
											>
												<Link
													to={`/category/${category.slug}`}
													className="category-item"
													style={{
														minWidth: "100%"
													}}
												>
													{dt(category.name)}
												</Link>
												<div
													className="col-12 p-0"
													style={{
														columnCount: "2"
													}}
												>
													{category.children
														.slice(0, 22)
														.map(item => {
															return (
																<Link
																	className="d-block"
																	style={{
																		color:
																			"black"
																	}}
																	to={
																		item.slug
																	}
																>
																	{item.name}
																</Link>
															);
														})}
												</div>
											</div>
										);
									})}
								</Fragment>
							))}
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
